import React, { useEffect, useState } from "react";
import Card from "./Card";
import { User } from "@/interfaces/userInterface";
import axios from "axios";

interface UserInterfaceProps {
  backendName: string;
}

const UserInterface = ({ backendName }: UserInterfaceProps) => {
  const apiUrl = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";
  const [users, setUsers] = useState<User[]>([]);
  const [newUsers, setNewUsers] = useState({ name: "", email: "" });
  const [updateUser, setUpdateUser] = useState({ id: "", name: "", email: "" });

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const res = await axios.get(`${apiUrl}/api/${backendName}/users`);
        setUsers(res.data.reverse());
      } catch (error) {
        console.log("Error fetching users:", error);
      }
    };
    fetchUsers();
  }, [apiUrl, backendName]);

  return (
    <div className={`p-4 bg-sky-400 min-h-screen ${backendName}`}>
      <h1 className="text-2xl font-bold">User Information</h1>

      <div className="space-y-2">
        {users.map((user) => (
          <div key={user.id}>
            <Card user={user} />
            <button className="mt-2 bg-red-500 text-white px-4 py-2 rounded">
              Delete
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default UserInterface;
